
body{
  margin: 0;
  padding: 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #333;
}

.index{
    color:skyblue;
}
.index img{
    width:.3rem;
}

.head{
    width:100%;
    height:1rem;
    line-height:1rem;
    font-size:.28rem; 
    background: -webkit-gradient(linear,left top, left bottom,from(rgba(180, 249, 172,1)),color-stop(rgba(29, 253, 2, .5)),to(rgba(180, 249, 172,1))); 
    background: linear-gradient(to bottom,rgba(180, 249, 172,1),rgba(29, 253, 2, .5),rgba(180, 249, 172,1)); 
    color:#fff;
}

.foot{
    font-size:.24rem;
    color:pink;
}
